export const what_we_do_body = "Our team is dedicated to investigating and evaluating discrepencies in COVID-19 mortality throughout the US. We are an interdisciplinary team of public health researchers, demographers, and statisticians who are investigating the qualtiy of death certificate data across local areas of the United States. Our work compares reported COVID-19 death tallies to estimates of excess mortality in order to identify areas of the country with potential underreporting."
